.card {
  transform-origin: center;
  width: 200px;
  height: 231px;
  letter-spacing: 0%;
  text-align: center;
  color: #2f4f4f;
  padding: 10px 10px 16px;
  line-height: 100%;
  transition: background-color, transform 1s;
  position: relative;
}

.card:hover {
  background-color: #eee;
  border-radius: 12px;
  transform: scale(1.2);
}

.card__image {
  margin-bottom: 8px;
  position: relative;
}

.card__image-image {
  object-fit: cover;
  object-position: center;
  z-index: 0;
  border-radius: 200px;
  width: 200px;
  height: 200px;
  display: block;
}

.card__details-name {
  margin-bottom: 8px;
  font-size: 20px;
  font-weight: 700;
  display: block;
}

.card__image-rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 100%;
  width: 32px;
  height: 32px;
  margin-bottom: 9px;
  font-size: 16px;
  font-weight: 700;
  line-height: 32px;
  position: absolute;
  top: 84px;
  right: 10px;
}

.card__extra {
  opacity: 0;
  color: #000;
  font-weight: 400px;
  font-size: 18x;
  letter-spacing: 0%;
  text-align: center;
  z-index: 2;
  background-color: #eee;
  border-radius: 12px;
  font-family: Arial;
}

.card:hover .card__extra {
  opacity: 1;
  padding-bottom: 16px;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td {
  font: inherit;
  vertical-align: baseline;
  border: 0;
  margin: 0;
  padding: 0;
}

html {
  font-family: Arial, sans-serif;
}

.page {
  --column-coun: 2;
  --inline-padding: 35px;
  padding: 30px var(--inline-padding);
  grid-template-columns: repeat(var(--column-coun), 200px);
  perspective: 400px;
  justify-content: center;
  gap: 30px 40px;
  display: grid;
}

@media (width >= 750px) {
  .page {
    --inline-padding: 160px;
  }
}

@media (width >= 1000px) {
  .page {
    --inline-padding: 55px;
    --column-coun: 4;
  }
}

@media (width >= 1500px) {
  .page {
    --inline-padding: 305px;
    --column-coun: 4;
  }
}
